.mb20 {
  margin-bottom: 20px;
}
.mb15 {
  margin-bottom: 15px;
}
.mt20 {
  margin-top: 20px;
}
.no-padding {
  padding: 0;
}
.app-container {
  position: relative;
}
.loading {
  position: fixed;
  z-index: 5;
  top: 50%;
  left: 0;
  width: 100%;
  display: flex;
  justify-content: center;
}
/* Weather Card */
.card-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 0 -15px;
}
.weather-card {
  background-color: #eef8fb;
  display: flex;
  flex: none;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  padding: 15px;
}
.weather-card .weather-card__city {
  font-size: 28px;
  margin: 0 -2px;
}
.weather-card .weather-card__country {
  font-size: 12px;
  display: inline-block;
  transform: translateY(-15px);
}
.weather-card .weather-card__temp {
  font-size: 48px;
}
.weather-card .weather-card__options {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.weather-card .weather-card__options .weather-card__options-row {
  display: flex;
  width: 100%;
  justify-content: space-between;
}
.weather-card .weather-card__option-name {
  font-size: 14px;
  color: #666;
}

/* Weather Forecast */
.weather-forecast {
  background-color: #eef8fb;
  padding: 15px;
}
.weather-forecast .weather-forecast__city {
  font-size: 20px;
}

/* Search Panel */
.search-panel {
  position: relative;
}
.search-panel .search-panel__item-list {
  position: absolute;
  z-index: 1;
  width: 100%;
  box-shadow: 1px 1px 1px rgba(0,0,0,0.2);
  top: 39px;
}
.search-panel .search-panel__item-list :first-child {
  border-top: none;
}
.search-panel .search-panel__item-list .list-group-item:hover {
  cursor: pointer;
  background-color: #f8f9fa;
}